body.debug:after {
  color: #fff;
  font-weight: bold;
  background-color: #d45959;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99999999;
  padding: 0 5px;
  position: fixed;
  content: "default"; }

/* max-width 640px, mobile-only styles */
@media only screen {
  body.debug:after {
    position: fixed;
    content: "small"; } }

/* min-width 641px and max-width 1024px */
@media only screen and (min-width: 40.063em) {
  body.debug:after {
    position: fixed;
    content: "medium"; } }

/* min-width 1025px and max-width 1440px */
@media only screen and (min-width: 64.063em) {
  body.debug:after {
    position: fixed;
    content: "large"; } }

/* min-width 1441px and max-width 1920px */
@media only screen and (min-width: 90.063em) {
  body.debug:after {
    position: fixed;
    content: "xlarge"; } }

/* min-width 1441px */
@media only screen and (min-width: 120.063em) {
  body.debug:after {
    position: fixed;
    content: "xxlarge"; } }

